import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Link, graphql } from "gatsby";
import Image from "../components/image"

export default ({ data }) => {
  const edges = data.allSitePage.edges
  var items = edges.filter(({ node }) => {
    return !!(node && node.context && node.context.name)
  })

  var is_mobile = false
  if (typeof window !== 'undefined') {
    is_mobile = window.innerWidth <= 700
  }

  return (
    <Layout>
      <SEO title="Home" keywords={[`gatsby`, `application`, `react`]} />
      <div style={{ textAlign: 'center' }}>
        {items.map(({ node }, index) => {
          return <div
            key={index}
            style={{
              position: 'relative',
              width: is_mobile ? '90%' : '272px',
              height: 272 + 32 + 'px',
              display: 'inline-block',
              margin: '8px',
            }}
          >
            <Link to={node.path}>
              <Image
                style={{
                  width: '100%',
                  height: '272px',
                  borderRadius: '2px 2px 0 0',
                }}
                src={node.context.name.replace(/ |\./g, '') + '.jpg'}
              />
              <p
                style={{
                  height: '32px',
                  lineHeight: '32px',
                  verticalAlign: 'middle',
                  textAlign: 'center',
                  textDecoration: 'none',
                  color: 'white',
                  backgroundColor: '#212121',
                  borderRadius: '0 0 2px 2px',
                }}
              >
                {node.context.name}
              </p>
              <div
                onMouseEnter={(e) => e.target.style.opacity = '0.2'}
                onMouseLeave={(e) => e.target.style.opacity = '0'}
                style={{
                  backgroundColor: 'lavenderblush',
                  opacity: '0',
                  position: 'absolute',
                  top: 0,
                  right: 0,
                  bottom: 0,
                  left: 0,
                }}
              ></div>
            </Link>
          </div>
        })}
      </div>
    </Layout>
  )
}

export const query = graphql`
  query {
    allSitePage {
      edges {
        node {
          path
          context {
            name
            birth
            death
            about
            link
          }
        }
      }
    }
  }
`
